import BhtBanner from '@/components/common/bht-banner'
import NewsListCom from '@/components/news/news-list-com.vue'
import {
	newsList
} from '@/api/news.js'
export default {
	components: {
		BhtBanner,
		NewsListCom
	},
	data() {
		return {
			downOption: {
				autoShowLoading: true,
				textInOffset: '下拉即可刷新...',
				textOutOffset: '松开即可刷新...',
				textLoading: '努力加载中...'
			},
			upOption: {
				auto: true,
				noMoreSize: 5,
				empty: {
					tip: '没有查询到数据',
					icon: '',
				},
				textNoMore: '没有更多数据了'
			},
			dataList: [],
			params: {}
		}
	},
	methods: {
		//获取mescroll对象
		initMescroll(mescroll) {
			this.mescroll = mescroll;
		},
		//下拉刷新
		downCallback(mescroll) {
			mescroll.resetUpScroll();
		},
		//上拉刷新
		upCallback(mescroll) {
			this.params.page = mescroll.num;
			this.params.limit = mescroll.size;
			newsList(this.params).then(res => {
				let {
					count,
					data
				} = res.data
				if (mescroll.num == 1) this.dataList = [];
				this.dataList = this.dataList.concat(data);
				mescroll.endBySize(
					data.length, count);
				this.$nextTick(() => {
					mescroll.endSuccess(data.length)
				})
			})
		}
	}
}
